//
// styles/_portfolio.scss
//

@import 'reference/mixins';
@import 'reference/variables';

.portfolio {
	@include clear-fix;

	&__list {
		@include clean-list;

		margin: 0 auto;
	}

	&__item {
		box-sizing: border-box;
		float: left;
		text-align: center;
		width: 100%;

		@include media-min(sm) {
			padding: 0 10px;
			width: (1 / 2) * 100%;

			&:nth-child(2n + 1) {
				clear: left;
			}
		}

		@include media-min(md) {
			width: (1 / 3) * 100%;

			&:nth-child(2n + 1) {
				clear: none;
			}

			&:nth-child(3n + 1) {
				clear: left;
			}
		}
	}

	&__link {
		$box-shadow: 2px;

		background-color: $white;
		box-shadow: $box-shadow $box-shadow $box-shadow rgba($grey-darker, .1);
		display: inline-block;
		margin-bottom: 20px;
		max-width: 100%;
		padding: 10px;
		text-decoration: none;
		transition: box-shadow .3s ease-out;

		&,
		&:visited {
			color: $grey-dark;
		}

		&:focus,
		&:hover {
			$box-shadow: 2px;

			box-shadow: $box-shadow $box-shadow $box-shadow rgba($grey-darker, .3);
			color: $red;
		}
	}

	&__frame {
		$height: 180px;

		height: auto;
		margin-bottom: 10px;
		max-width: 100%;

		.lt-ie9 & {
			height: $height;
			max-width: none;
			width: 280px;
		}
	}

	&__image {
		height: auto;
		max-width: 100%;

		.lt-ie9 &[data-src],
		.no-js & {
			display: none;
		}

		.no-js noscript & {
			display: block;
		}
	}

	&__title {
		display: block;
	}

	&__desc {
		color: $grey;
		display: block;
		margin-top: 5px;
	}
}
